"use strict";

/**
 *  2 
 * @param {Array<String>} a1  ["zho", "cho"]
 * @param {Array<String>} a2  ["yng"]
 * @return {Array<String>}  ["zhoyng", "choyng"]
 */
function combo2array(a1, a2) {
  const result = [];
  if (!a1.length) {
    return a2;
  }
  if (!a2.length) {
    return a1;
  }
  for (let i = 0, l = a1.length; i < l; i++) {
    for (let j = 0, m = a2.length; j < m; j++) {
      result.push(a1[i] + a2[j]);
    }
  }
  return result;
}

/**
 * 
 * @param {Array<Array<String>>} arr  [["zho", "cho"], ["yng"], ["dng"], ["shng"]]
 * @return {Array<String>} 
 *  [
 *    ["zhoyng"], ["dng"], ["shng"],
 *    ["choyng"], ["dng"], ["shng"]
 *  ]
 */
function combo(arr) {
  if (arr.length === 0) {
    return [];
  }
  if (arr.length === 1) {
    return arr[0];
  }
  let result = combo2array(arr[0], arr[1]);
  for (let i = 2, l = arr.length; i < l; i++) {
    result = combo2array(result, arr[i]);
  }
  return result;
}

/**
 * 
 * @param {string[]|string[][]} arr1 eg: ["hai", "huan"]
 * @param {Array<String>} arr2 eg: ["qian"]
 * @returns {string[][]} eg: [ ["hai", "qian"], ["huan", "qian"] ]
 */
function compact2array(a1, a2) {
  if (!Array.isArray(a1) || !Array.isArray(a2)) {
    throw new Error("compact2array expect two array as parameters");
  }
  if (!a1.length) {
    a1 = [""];
  }
  if (!a2.length) {
    a2 = [""];
  }
  const result = [];
  for (let i = 0, l = a1.length; i < l; i++) {
    for (let j = 0, m = a2.length; j < m; j++) {
      if (Array.isArray(a1[i])) {
        result.push([...a1[i], a2[j]]);
      } else {
        result.push([a1[i], a2[j]]);
      }
    }
  }
  return result;
}

function compact(arr) {
  if (arr.length === 0) {
    return [];
  }
  if (arr.length === 1) {
    return [arr[0]];
  }
  let result = compact2array(arr[0], arr[1]);
  for (let i = 2, l = arr.length; i < l; ++i) {
    result = compact2array(result, arr[i]);
  }
  return result;
}

exports.combo2array = combo2array;
exports.combo = combo;
exports.compact2array = compact2array;
exports.compact = compact;
